:root {
  --menu--width: 250px;
}

.topnav {
  background-color: #f4efed;
  color:#232123;
  font-family: 'Spartan', sans-serif;
  padding: 20px;
}

.topnav {
  align-items: center;
  display: flex;
  justify-content: space-between;
}

.topnav .logo {
  align-items: center;
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.topnav button.menu {
  background-color: transparent;
  border: none;
  cursor: pointer;
  outline: none;
}

.topnav img.logo {
  width: 200px;
  height: auto;
}

.menu-close {
  background-color: transparent;
  border: none;
  color: #ffffff;
  align-self: flex-end;
}

.menu-close svg {
  fill: #ffffff;
}

.navbar .links {
  display: none;
}

.navbar.is-open .links {
  background-color: #000000;
  bottom: 0;
  display: flex;
  flex-direction: column;
  padding: 20px;
  position: fixed;
  right: 0;
  top: 0;
  width: var(--menu--width);
  z-index: 10;
}

.navbar.is-open .links a {
  color: #ffffff;
  font-size: 1rem;
  margin-bottom: 1rem;
}

.navbar .links a {
  color: #000000;
  font-size: 0.875rem;
  text-decoration: none;
}

.navbar .links a:hover {
  color: #ff3e71;
}

@media screen and (min-width: 1024px) {
  .menu-close {
    display: none;
  }
  
  .navbar.is-open {
    width: 100%;
  }

  .topnav .logo {
    width: auto;
  }

  .topnav button.menu {
    display: none;
  }

  .navbar .links,
  .navbar.is-open .links {
    align-items: center;
    background-color: transparent;
    display: flex;
    flex-direction: row;
    margin-bottom: 0;
    padding: 0;
    position: static;
    width: auto;
  }

  .navbar.is-open .links a {
    color: #000000;
    margin-bottom: 0;
    font-size: 0.875rem;
  }

  .navbar .links a {
    margin-right: 20px;
  }

  .navbar .links a:last-child {
    margin-right: 0;
  }
}